import React from "react";
import { auth } from "../firebase";
import {
  signInWithEmailAndPassword,
  createUserWithEmailAndPassword,
} from "firebase/auth";
import "bootstrap/dist/css/bootstrap.min.css";
import Button from "react-bootstrap/Button";

class AuthForm extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      name: "",
      nameInput: "",
      email: "",
      password: "",
    };
  }

  signInUpValidation = () => {
    if (this.state.password.length < 6) {
      return alert("Password must be at least 6 characters");
    }
  };

  handleSignUpClick = async () => {
    this.signInUpValidation();

    await createUserWithEmailAndPassword(
      auth,
      this.state.email,
      this.state.password
    );
    this.setState = {
      email: "",
      password: "",
    };
  };

  handleSignInClick = async () => {
    this.signInUpValidation();

    await signInWithEmailAndPassword(
      auth,
      this.state.email,
      this.state.password
    );

    this.setState = {
      email: "",
      password: "",
    };
  };

  render() {
    return (
      //Input for user to sign up or sign in
      <div>
        <label>Email</label>
        <br />
        <input
          type="email"
          value={this.state.email}
          onChange={(e) => this.setState({ email: e.target.value })}
          placeholder="Enter email"
        />
        <br />
        <br />
        <label>Password</label>
        <br />
        <input
          type="password"
          value={this.state.password}
          onChange={(e) => this.setState({ password: e.target.value })}
          placeholder="Enter password"
        />
        <br />
        <br />
        <Button variant="secondary" onClick={this.handleSignUpClick}>
          SignUp
        </Button>{" "}
        {""}
        <Button variant="secondary" onClick={this.handleSignInClick}>
          SignIn
        </Button>
        <br />
        <br />
      </div>
    );
  }
}

export default AuthForm;
