import React from "react";
import { auth } from "./firebase";
import { onAuthStateChanged, signOut } from "firebase/auth";

import "./App.css";
import AuthForm from "./Component/AuthForm";
import UploadPost from "./Component/UploadPost";
import Newsfeed from "./Component/Newsfeed";
import NavBar from "./Component/NavBar";

// Save the Firebase message folder name as a constant to avoid bugs due to misspelling
const DB_MESSAGES_KEY = "messages";

class App extends React.Component {
  constructor(props) {
    super(props);
    // Initialise empty messages array in state to keep local state in sync with Firebase
    // When Firebase changes, update local state, which will update local UI
    this.state = {
      user: {},
      loggedIn: false,
    };
  }

  componentDidMount() {
    onAuthStateChanged(auth, (user) => {
      console.log(user);
      if (user) {
        this.setState({
          user: user,
          loggedIn: true,
        });
      }
    });
  }

  render() {
    return (
      <div className="App">
        <NavBar
          userId={this.state.user.email}
          signOut={(e) => {
            signOut(auth);
            this.setState({
              user: {},
              loggedIn: false,
            });
          }}
        />
        <header className="App-header">
          {/* Input box for user to enter name, Entered name will be registered in current session */}
          {!this.state.loggedIn && <AuthForm />}

          {/* Input form for user to enter post message, upload photo*/}
          {this.state.loggedIn && (
            <div>
              <UploadPost
                DB_MESSAGES_KEY={DB_MESSAGES_KEY}
                userEmail={this.state.user.email}
              />
            </div>
          )}

          {/* Renders Newsfeed*/}
          <Newsfeed
            messages={this.state.messages}
            DB_MESSAGES_KEY={DB_MESSAGES_KEY}
            loggedIn={this.state.loggedIn}
          />
        </header>
      </div>
    );
  }
}

export default App;
