import React from "react";
import { push, ref as realTimeDatabaseRef, set } from "firebase/database";
import { realTimeDatabase, storage } from "../firebase";
import {
  getDownloadURL,
  ref as storageRef,
  uploadBytesResumable,
} from "firebase/storage";

import "bootstrap/dist/css/bootstrap.min.css";
import ProgressBar from "react-bootstrap/ProgressBar";
import Button from "react-bootstrap/Button";

const STORAGE_KEY = "images/";

class UploadPost extends React.Component {
  constructor(props) {
    super(props);
    // Initialise empty messages array in state to keep local state in sync with Firebase
    // When Firebase changes, update local state, which will update local UI
    this.state = {
      messageInput: "",
      fileInputFile: { name: "" },
      fileInputValue: "",
      uploadProgress: 0,
    };
  }

  handleMessageChange = (e) => {
    this.setState({ messageInput: e.target.value });
  };

  handlePhotoUpload = (e) =>
    // e.target.files is a FileList object that is an array of File objects
    // e.target.files[0] is a File object that Firebase Storage can upload
    this.setState({
      fileInputFile: e.target.files[0],
      fileInputValue: e.target.file,
    });

  writeData = (url) => {
    const PostRef = realTimeDatabaseRef(
      realTimeDatabase,
      this.props.DB_MESSAGES_KEY
    );
    const newPostRef = push(PostRef);

    set(newPostRef, {
      userEmail: this.props.userEmail,
      message: this.state.messageInput,
      dateTime: new Date().toLocaleString(),
      url: url,
    });

    this.setState({
      messageInput: "",
      fileInputFile: { name: "" },
      fileInputValue: "",
    });
  };

  handlePostSubmit = (e) => {
    e.preventDefault();

    const fullStorageRef = storageRef(
      storage,
      STORAGE_KEY + this.state.fileInputFile.name
    );

    const uploadTask = uploadBytesResumable(
      fullStorageRef,
      this.state.fileInputFile
    );

    // if user selected a photo, upload it. Else, just write the message
    this.state.fileInputFile.name !== ""
      ? uploadTask.on(
          "state_changed",
          (snapshot) => {
            // Observe state change events such as progress, pause, and resume
            // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
            const progress =
              (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
            this.setState({
              uploadProgress: progress,
            });
          },
          (error) => {
            console.log(error);
          },
          () => {
            getDownloadURL(
              uploadTask.snapshot.ref,
              this.state.fileInputFile.name
            ).then((url) => {
              this.writeData(url);
            });
          }
        )
      : this.writeData("");
  };

  render() {
    return (
      <div>
        {this.state.uploadProgress === 0 ||
        this.state.uploadProgress === 100 ? (
          <form onSubmit={this.handlePostSubmit}>
            {/* message input */}
            <br />
            <h3>Message</h3>
            <input
              type="text"
              value={this.state.messageInput}
              onChange={this.handleMessageChange}
            />{" "}
            <br />
            <br />
            {/* photo upload */}
            <div className="inputContainer">
              <label>
                {this.state.fileInputFileName !== ""
                  ? "Change Photo"
                  : "Select Photo"}{" "}
                <br />
                <br />
                <i className="fa fa-2x fa-camera"></i>
                <input
                  className="inputTag"
                  type="file"
                  // accept="image/png, image/jpg, image/gif, image/jpeg"
                  // Set state's fileInputValue to "" after submit to reset file input
                  value={this.state.fileInputValue}
                  onChange={this.handlePhotoUpload}
                />
                <br />
              </label>
            </div>
            <br />
            <div className="inputContainer">
              {this.state.fileInputFile.name !== ""
                ? `Photo selected: ${this.state.fileInputFile.name}`
                : "No file selected"}
            </div>
            <br />
            <Button type="submit" value="Post">
              Post
            </Button>
          </form>
        ) : (
          <div>
            <br />
            <h3>Posting...</h3>
            <br />
            <ProgressBar animated now={this.state.uploadProgress} />
          </div>
        )}
        <br />
      </div>
    );
  }
}

export default UploadPost;
