import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import Container from "react-bootstrap/Container";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";

class NavBar extends React.Component {
  render() {
    return (
      <Navbar
        expand="lg"
        style={{ width: "100%" }}
        collapseOnSelect
        bg="dark"
        variant="dark"
      >
        <Container>
          <Navbar.Brand>
            <h1>Instantgram</h1>
          </Navbar.Brand>
          <h4>
            {this.props.userId !== undefined ? (
              <Navbar.Text>
                Signed in as:
                <a
                  href="https://www.google.com/"
                  target="_blank"
                  rel="noreferrer"
                >
                  {this.props.userId}{" "}
                </a>
              </Navbar.Text>
            ) : (
              <Navbar.Text>Sign in to post</Navbar.Text>
            )}
          </h4>
          <Navbar.Toggle />
          <Navbar.Collapse>
            <Nav>
              <Nav.Link href="#home">Home</Nav.Link>
              <Nav.Link href="#profile">Profile</Nav.Link>
              <Nav.Link onClick={this.props.signOut} href="#logout">
                Logout
              </Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    );
  }
}

export default NavBar;
